/* Apply styles to the container */
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* Center content horizontally */
}

/* Apply styles to the header */
.header {
  text-align: center;
  margin-bottom: 40px;
}

/* Apply styles to the title */
.title {
  font-size: 48px;
  color: #333;
  font-weight: bold;
}

/* Apply styles to the content */
.content {
  width: 100%;
}

/* Apply styles to the tools section */
.task-section {
  margin-top: 60px;
}

/* Apply styles to the section title */
.section-title {
  font-size: 32px;
  margin-bottom: 20px;
  color: #333;
  font-weight: bold;
}

/* Apply styles to the task list */
.task-list {
  list-style: none;
  padding: 0;
  width: 100%;
}

/* Apply styles to the task item */
.task-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 2px solid #eee;
}

/* Apply styles to the task list buttons */
.task-list button {
  margin-left: 20px;
  background-color: #28a745;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 16px;
}

.task-list button:hover {
  background-color: #218838;
}

/* Apply styles to the delete button */
.delete-button {
  background-color: #ff4444;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 16px;
}

.delete-button:hover {
  background-color: #cc0000;
}

/* Apply styles to the task management buttons */
.tool button {
  margin-right: 20px;
  background-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 16px;
}

.tool button:hover {
  background-color: #0056b3;
}

/* Apply styles to the completed task item */
.task-item.completed {
  text-decoration: line-through;
  color: #999;
}

/* Apply styles to the filter section */
.filter {
  margin-top: 40px;
  text-align: center;
}

/* Apply styles to the filter buttons */
.filter button {
  margin-right: 20px;
  background-color: #333;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 16px;
}

.filter button:hover {
  background-color: #555;
}

/* Apply styles to the footer */
.footer {
  text-align: center;
  margin-top: 40px;
  color: #666;
  font-size: 18px;
}